import React, { Component } from 'react';
import {Link} from "react-router-dom";
import { connect } from 'react-redux';
import {actionCreators} from '_action';
import {PageContainer,ButtonRounded,Slider,Container,ModalWindow} from 'components';
import {imgAsset} from 'helper';

import {ItemBar} from './ItemBar';
import {TopBar} from './TopBar';

let isMounted;
class ProdukList extends Component {
  constructor(props) {
    super(props);
    this.state={
      windowType:'LOGIN_MODAL',
      data:[],
      dataCategory:[],
      dataType:[],
      categoryPos:0,
      typePos:0,
      searchKey:'',
    }
  }
  componentWillMount=()=>{
    isMounted=true;
  }
  componentWillUnMount=()=>{
    isMounted=false;
  }
  componentDidMount=()=>{
    document.scrollingElement.scrollTop=0;
  }

  onSearch=(value)=>{
    this.setState({searchKey:value});
  }
  render() {
    const {state} = this;
    return (
      <PageContainer onLoginBtnClick={this.showModalLogin} activePos={0}>
        <TopBar onSearch={this.onSearch}/>
        <Container className={"project-container fox-flex fox-wrap"}>
          <ItemBar data={state.data} categoryPos={state.categoryPos} typePos={state.typePos} searchKey={state.searchKey}/>
        </Container>
      </PageContainer>
    );
  }
}

const mapStateToProps = (state) => ({
  ...state.UserLogin,
})
const mapDispatchToProps = (dispatch) => ({
  //setPrevTabbar: (tabbar) => dispatch(actionCreators.previousTabbar(tabbar)),
})

export default connect(mapStateToProps, mapDispatchToProps)(ProdukList);
