import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
  Redirect
} from 'react-router-dom';


import {actionCreators} from '_action';
import {PageContainer,ModalWindowLogin,Container} from 'components';

class SignInPages extends Component {
  componentDidMount=()=>{
    document.scrollingElement.scrollTop=0;
  }
  render() {
    if(this.props.userToken!==null){
      return(
        <Redirect
          to={{
            pathname: "/board",
            state: { from: "/sign-in" }
          }}
        />
      )
    }else{
      return (
        <PageContainer activePos={4} isLoginPage={true}>
          <Container>
            <div className={"max-width-480 login-page modal-content"}>
              <ModalWindowLogin windowType={"LOGIN_PAGE"}/>
            </div>
          </Container>
        </PageContainer>
      );
    }

  }
}

const mapStateToProps = (state) => ({
  ...state.UserLogin,
})
const mapDispatchToProps = (dispatch) => ({
  SetUserToken: (data) => dispatch(actionCreators.SetUserToken(data)),
})

export default connect(mapStateToProps, mapDispatchToProps)(SignInPages);
