export const ResizeImage = async(input,maxWidth,callback)=> {
  if (input) {
    var reader = new FileReader();
    reader.onload = async function(event) {
      var img = new Image();
      img.onload = async function() {
        var canvas = document.createElement('canvas');
        var canvas2d = canvas.getContext('2d');

        var originalWidth   = img.width;
        var originalHeight  = img.height;

        var newRatio 	  = maxWidth / originalWidth;
        var newWidth 		= maxWidth;
        var newHeight		= originalHeight * newRatio;

        if (img.width <= maxWidth) {
          newWidth  = img.width;
          newHeight = img.height;
        }

        canvas.width 	= newWidth;
        canvas.height = newHeight;
        await canvas2d.drawImage(img, 0, 0, newWidth, newHeight);
        callback(canvas.toDataURL("image/jpeg"));

      };
      img.src = event.target.result;
    };
    await reader.readAsDataURL(input);
  }else{
    return null;
  }
}

