let isMounted;
class LayananPage extends Component {
  constructor(props) {
    super(props);
    this.state={
      windowType:'LOGIN_MODAL',
      dataListing:[],
      redirectTo:'/board',
    }
  }
  componentWillMount=()=>{
    isMounted=true;
  }
  componentDidMount=()=>{
    document.scrollingElement.scrollTop=0;
    this.onGetListing();
  }
  showModalLogin=(windowType)=>{
    this.setState({windowType},()=>{
      this.modalLogin.show();
    })
  }
  onGetListing=()=>{
    actionCreators.GetProductList({key:''},({data,isFetching,isFound,isError})=>{
      this.setState({isFound,isFetching,isError});
      if(isMounted){
        if(isFound){
          this.setState({dataListing:data.data,isFetching})
        }else if(isError){
          setTimeout(()=>{
            this.onGetListing();
          },5000);
        }
      }
    })
  }
  render() {
    const {state,props} = this;
    return (
      <PageContainer activePos={2}>
        <Container>
          <Banner />
          <WhatWeDo />
          <WhatWeDoInvestor/>
          <OurProject data={state.dataListing} isFetching={state.isFetching} isFound={state.isFound} isError={state.isError}/>
        </Container>

      </PageContainer>
    );
  }
}

const mapStateToProps = (state) => ({
  ...state.UserLogin,
})
const mapDispatchToProps = (dispatch) => ({
  //setPrevTabbar: (tabbar) => dispatch(actionCreators.previousTabbar(tabbar)),
})

export default connect(mapStateToProps, mapDispatchToProps)(LayananPage);
