import React, { Component } from 'react';
import {Link} from "react-router-dom";
import { connect } from 'react-redux';
import {actionCreators} from '_action';
import {PageContainer,Container,ButtonRounded,RoundedBox,ModalWindow,ModalWindowLogin,LoaderProjectDetile} from 'components';
import {imgAsset} from 'helper';

import {Tabbar} from './Tabbar';
import {ProdukMore} from './ProdukMore';
import {ProdukBanner} from './ProdukBanner';
import {ProdukTag} from './ProdukTag';

let isMounted;
class ProdukViewPage extends Component {
  constructor(props) {
    super(props);
    this.state={
      activePos:0,
      windowType:'LOGIN_MODAL',
      tabbar:[{title:'Descriptions'},{title:'FAQ'},{title:'Busines Analitycs'}],
      data:{},
    }
  }
  componentWillMount=()=>{
    isMounted=true;
  }
  componentWillUnMount=()=>{
    isMounted=false;
  }
  componentDidMount=()=>{
    this.onGetProduct(this.props);
    document.scrollingElement.scrollTop=0;
  }
  onTabbarClick=(value)=>{
    this.setState({activePos:value});
  }
  showModalLogin=(windowType)=>{
    this.setState({windowType},()=>{
      this.modalLogin.show();
    })
  }
  componentWillReceiveProps=(nextProps)=>{
    if(this.props.match.params.id!==nextProps.match.params.id){
      this.onGetProduct(nextProps);document.scrollingElement.scrollTop=0;
    }
  }

  onGetProduct=(props)=>{
    actionCreators.GetProductDetail({id:props.match.params.id},({data,isFetching,isFound,isError})=>{
      if(isMounted){
        this.setState({isFound,isFetching:true,isError});
        if(isFound){
          this.setState({data:data.data,isFetching},()=>{
            this.onGetListing();
          });
        }else if(isError){
          setTimeout(()=>{
            this.onGetProduct(props);
          },5000)
        }
      }
    })
  }
  onGetListing=()=>{
    actionCreators.GetProductList({keywords:''},({data,isFetching,isFound,isError})=>{
      if(isMounted){
        this.setState({isFoundListing:isFound,isFetchingListing:true,isErrorListing:isError});
        if(isFound){
          this.setState({dataListing:data.data,isFetching})
        }else if(isError){
          setTimeout(()=>{
            this.onGetListing();
          },5000);
        }
      }
    })
  }
  render() {
    const {state} = this;
    return (
      <PageContainer>
        <Container>
          <LoaderProjectDetile isFetching={state.isFetching}>
            {state.isFound && state.data!==null &&
              <>
                <RoundedBox style={{marginTop: '20px'}}>
                  <ProdukBanner data={state.data}/>
                </RoundedBox>

                <RoundedBox>
                  <Tabbar data={state.data}>
                    <ProdukMore data={state.dataListing}/>
                  </Tabbar>
                </RoundedBox>
              </>
            }
          </LoaderProjectDetile>
        </Container>
      </PageContainer>
    );
  }
}

const mapStateToProps = (state) => ({
  ...state.UserLogin,
})
const mapDispatchToProps = (dispatch) => ({
  //setPrevTabbar: (tabbar) => dispatch(actionCreators.previousTabbar(tabbar)),
})

export default connect(mapStateToProps, mapDispatchToProps)(ProdukViewPage);
