import React from 'react';
import {Container,RoundedBox} from 'components';
import {OwnIcon} from 'helper';

const NotFound=(props)=>{
  if(props.isShow){
    return(
      <RoundedBox className={"bg-gray-soft not-found"}>
        <div className={"boxes-error d-flex flex-column align-items-center"}>
          <OwnIcon.IconSad className={"icon-sadness"} />
          <div className={"text"}>Maaf Sepertinya yang anda cari tidak ditemukan, silahkan cari dengan kata kunci yang lain</div>
        </div>
      </RoundedBox>
    )
  }else{
    return(null)
  }
}
NotFound.defaultProps={
  isShow:false,
}
export {NotFound};
