import React, { Component } from 'react';
import {
  BrowserRouter,
  Switch,
  Route
} from 'react-router-dom';

// Import pages component here
import {ProtectedRoute,OutLinkRoute} from './Handler';
import Error404 from 'pages/404';
import SignInPages from 'pages/SignInPages';
import ProdukViewPage from 'pages/ProdukViewPage';
import ProdukList from 'pages/ProdukList';
import HomePage from 'pages/HomePage';
import TentangPage from 'pages/TentangPage';
import LayananPage from 'pages/LayananPage';
import TestPage from 'pages/TestPage';
import FaqsPage from 'pages/FaqsPage';
import KontakPage from 'pages/KontakPage';

import {HomeBoard,CreateProdukBoard,ProdukListBoard} from 'pages/Dashboard';

// Global stylesheet
import 'assets/sass/app.scss';

class RouterMap extends Component {
  render() {
    return (
      <BrowserRouter>
        <Switch>
          <Route path="/"  component={HomePage} exact/>
          <Route path="/sign-in" component={SignInPages}/>
          <Route path="/:title/:id/view/" component={ProdukViewPage}/>
          <Route path="/produk/" component={ProdukList}/>
          <Route path="/tentang/" component={TentangPage}/>
          <Route path="/layanan/" component={LayananPage}/>
          <Route path="/faqs/" component={FaqsPage}/>
          <Route path="/kontak/" component={KontakPage}/>
          <OutLinkRoute path="/redirect/:link"/>

          <ProtectedRoute path="/board/" component={HomeBoard}/>
          <ProtectedRoute path="/board-create-produk" component={CreateProdukBoard}/>
          <ProtectedRoute path="/board-produk" component={ProdukListBoard}/>
          <Route path="/test" component={TestPage}/>
          <Route component={Error404}/>
        </Switch>
      </BrowserRouter>
    );
  }
}

export default RouterMap;
