import * as types from '../types'
import {PostRequestServer} from 'helper';
import {LOGIN_MANUAL} from 'config/api';

export const UserLogin = ({token,password,email},callback) => {
  let formData = new FormData();
      formData.append('email',email);
      formData.append('password',password);
  PostRequestServer({
      apiUrl    :LOGIN_MANUAL.apiUrl,
      baseUrl   :LOGIN_MANUAL.baseUrl,
      token     :token,
      requestTo :LOGIN_MANUAL.requestTo,
      data      :formData,
  },
  (dataResponse,{isFetching,isFound,isError,onLoading})=>{
    const {data} = dataResponse;
    callback({
      data,
      isFetching,
      isFound,
      isError,
      onLoading,
    });
  });
}


export const SetUserLogin = ({userToken,userData},comand) => async(dispatch)=>{
  if(comand==='create'){
    console.log(userToken);
    await dispatch({
      type: types.SET_USER_TOKEN,
      userToken,
      userData,
    })
  }else{
    dispatch({
      type: types.DEL_USER_TOKEN,
    })
  }
}
