import apisauce from 'apisauce';

export const PostRequestServer = ({baseUrl,apiUrl,token,data,requestTo},callback) =>{
  const api = apisauce.create({
    baseURL: baseUrl,
    timeout: 10000,
    headers:{
      'Access-Control-Allow-Origin':'*',
      'Access-Control-Allow-Headers':'*',
      'Access-Control-Allow-Methods':'POST',
      'Access-Control-Allow-Credentials':'true',
      //'Host':'https://foxdreamer.000webhostapp.com',
      'Cache-Control':'no-cache',
      //'Accept':'*/*',
      'Content-Type':'application/x-www-form-urlencoded',
      //'Content-Encoding':'gzip, deflate',
      //'Connection':'keep-alive',
      'Accept': 'application/json',
      'Authorization':token,
      'request-api-methode':requestTo,
      'x-api-key':'123',
    }
  });
  callback({
      data:null,
    },
    {
      isFetching:true,
      isFound:false,
      isError:false,
    }
  );
  api.post(apiUrl,data,{
    onUploadProgress:(progressEvent) => {
      const totalLength = progressEvent.lengthComputable ? progressEvent.total : 0;
      if (totalLength !== null) {
        //this.updateProgressBarValue(Math.round( (progressEvent.loaded * 100) / totalLength ));
        callback({
          data:null,
        },
        {
          isFetching:true,
          isFound:false,
          isError:false,
          onLoading:(Math.round( (progressEvent.loaded * 100) / totalLength )),
        });
      }
    }
  })
  .then((res) => {
    console.log(res,'server1');
    let data =res.data;
    if(res.ok){
      if(data.status!=='error'){
        if(data.constructor()!==String){
          callback({
              data:res.data,
            },
            {
              isFetching:false,
              isFound:true,
              isError:false,
            }
          );
        }else{
          callback({
              data:data,
            },
            {
              isFetching:false,
              isFound:false,
              isError:true,
            }
          );
        }
      }else{
        callback({
            data:data,
          },
          {
            isFetching:false,
            isFound:false,
            isError:true,
          }
        );
      }
    }else{
      callback({
          data:res,
        },
        {
          isFetching:false,
          isFound:false,
          isError:true,
        }
      );
    }
  })
  .catch((err) => {
    callback({
        data:err,
      },
      {
        isFetching:false,
        isFound:false,
        isError:true,
      }
    );
  })
}

export const FileRequestServer = ({baseUrl,apiUrl,token,data,requestTo},callback) =>{
  const api = apisauce.create({
    baseURL: baseUrl,
    timeout: 10000,
    headers:{
      'Content-Type': 'application/x-www-form-urlencoded',
      'Accept': 'application/json',
      'Authorization':token,
      'request-api-methode':requestTo,
      'x-api-key':'123'
    }
  });
  callback({
      data:null,
    },
    {
      isFetching:true,
      isFound:false,
      isError:false,
    }
  );
  api.post(apiUrl,data,{
    onUploadProgress:(progressEvent) => {
      const totalLength = progressEvent.lengthComputable ? progressEvent.total : progressEvent.target.getResponseHeader('content-length') || progressEvent.target.getResponseHeader('x-decompressed-content-length');
      if (totalLength !== null) {
        //this.updateProgressBarValue(Math.round( (progressEvent.loaded * 100) / totalLength ));
        callback({
          data:null,
        },
        {
          isFetching:true,
          isFound:false,
          isError:false,
          onLoading:(Math.round( (progressEvent.loaded * 100) / totalLength )),
        });
      }
    }
  })
  .then((res) => {
    let data =res.data;

    console.log(res,'server');
    if(data.constructor()!==String){
      callback({
          data:res.data,
        },
        {
          isFetching:false,
          isFound:true,
          isError:false,
        }
      );
    }else{
      callback({
          data:res.data,
        },
        {
          isFetching:false,
          isFound:false,
          isError:true,
        }
      );
    }
  })
  .catch((err) => {
    callback({
        data:err,
      },
      {
        isFetching:false,
        isFound:false,
        isError:true,
      }
    );
  })
}
