import React from 'react';
import {FlatList,ListingItem} from 'components';

const ProdukMore=(props)=>{
  return(
    <section className={"more-project"} >
      <div className={"more-header"}>
        <div className={"header-text"}>PRODUK LAINYA</div>
      </div>
      <div className={"more-content"}>
        <FlatList className={"more-column"}  data={props.data} render={({item,index})=>{
          return <ListingItem key={index.toString()} className={"more-item"} item={item} index={index} to={{pathname:`/project-invention-now/${item.id}/view`}}/>
        }} />
      </div>
    </section>
  )
}
ProdukMore.defaultProps={
  data:[],
}
export {ProdukMore};
