import React, { Component } from 'react';
import {Link} from "react-router-dom";
import { connect } from 'react-redux';
import {actionCreators} from '_action';
import {PageContainer,ButtonRounded,Slider,Container,ModalWindow,ModalWindowLogin,LeftBarDashboard,TopBar} from 'components';
import {imgAsset} from 'helper';

import ItemBar from './ItemBar';

class HomeBoard extends Component {
  constructor(props) {
    super(props);
    this.state={
      leftMenu:false,
      userData:props.userData,
    }
  }
  componentDidMount=()=>{
    document.scrollingElement.scrollTop=0;
    console.log(this.state.userData);
  }
  onTabbarClick=(value)=>{
    this.setState({activePos:value});
  }
  showModalLogin=(windowType)=>{
    this.setState({windowType},()=>{
      this.modalLogin.show();
    })
  }
  render() {
    const {state} = this;
    return (
      <PageContainer onLoginBtnClick={this.showModalLogin} activePos={4}>

        <TopBar onClick={()=>this.setState({leftMenu:!this.state.leftMenu})} title={"> User Info"}/>
        <Container className={"project-container d-flex flex-wrap bg-white board"}>
          <LeftBarDashboard isOpen={this.state.leftMenu}/>
          <ItemBar data={state.userData} />
        </Container>
      </PageContainer>
    );
  }
}

const mapStateToProps = (state) => ({
  ...state.UserLogin,
})
const mapDispatchToProps = (dispatch) => ({
  //setPrevTabbar: (tabbar) => dispatch(actionCreators.previousTabbar(tabbar)),
})

export default connect(mapStateToProps, mapDispatchToProps)(HomeBoard);
