import React, { Component } from 'react';
import { connect } from 'react-redux';
import {Footer} from './Footer';
import {Header} from './Header';

class PageContainer extends Component {
  render() {
    const {props} = this;
    return (
      <div className={"root"}>
        <Header userToken={props.userToken} activePos={this.props.activePos} onLoginBtnClick={this.props.onLoginBtnClick} isLoginPage={this.props.isLoginPage}/>
        <div style={{minHeight: '50vh',marginTop: '80px'}}>
          {this.props.children}
        </div>
        <Footer />
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  ...state.UserLogin,
})
const mapDispatchToProps = (dispatch) => ({
  //setPrevTabbar: (tabbar) => dispatch(actionCreators.previousTabbar(tabbar)),
})

PageContainer.defaultProps={
  activePos:null,
  onLoginBtnClick:()=>null,
  isLoginPage:false,
}
export default connect(mapStateToProps, mapDispatchToProps)(PageContainer);
