import React,{Component} from 'react';
import {OwnIcon,imgAsset,ResizeImage,ConvertBlobToImage} from 'helper';
import {LoaderElipsis} from 'components/loader';

class InputImage extends Component{
  constructor(props){
    super(props);
    this.state={
      isShow:false,
    };
  }
  onImage=(e)=>{
    e.stopPropagation();
    e.preventDefault();
    if(e.target.files[0]){
      ResizeImage(e.target.files[0],this.props.dimention,(blob)=>{
        if(blob){
          ConvertBlobToImage(blob,this.props.dimention,(img)=>{
            if(img){
              this.props.onChange(img);
              //this.props.onChange(URL.createObjectURL(img));
            }
          })
        }
      });
    }
  }
  render(){
    const {state,props}=this;
    if(props.isShow){
      if(props.type==='image'){
        return(
          <div className={'input-parent'}>
            {props.title!==null && <div className={"text-title"}>{props.title}</div> }
            <div className={"card-info fox-input"}>
              <div className={"card-input input-img pl-25 fox-flex fox-wrap fox-just-space fox-row"}>
                <div className={"card-img-box fox-col-2"}>
                  <div className={"fox-image"} style={props.style}>
                    <img alt={"indcrowd image"} className={props.objectFit} src={props.src!==(null||undefined)?(typeof props.src==='object'?URL.createObjectURL(props.src):props.src):imgAsset.noImg} />
                    <LoaderElipsis isFetching={props.isFetching} isAbsolute={true}>
                      { props.isDisabled!==true &&
                        <label className={"btn"}>
                          <input type="file" accept="image/*" onChange={this.onImage}/>
                          <OwnIcon.IconDownload className={"icon fox-anim"} />
                        </label>
                      }
                    </LoaderElipsis>
                  </div>
                </div>
                <div className={"fox-col-2-m text-info"}>{props.placeholder}</div>

              </div>
              {props.isError===true && <label className={"error"}>{props.errorText}</label> }
            </div>
          </div>
        )
      }else if(props.type==='card'){
        return(
          <div className={"fox-image"} style={props.style}>
            <img alt={"indcrowd image"} className={props.objectFit} src={props.src!==(null||undefined)?(typeof props.src==='object'?URL.createObjectURL(props.src):props.src):imgAsset.noImg} />
            <LoaderElipsis isFetching={props.isFetching} isAbsolute={true}>
              { props.isDisabled!==true &&
                <label className={"btn"} htmlFor="file">
                  <input type="file" accept="image/*" id="file" onChange={this.onImage}/>
                  <OwnIcon.IconDownload className={"icon fox-anim"} />
                </label>
              }
            </LoaderElipsis>
          </div>
        )
      }else{return null}
    }else{
      return null;
    }
  }
}
InputImage.defaultProps={
  onChange:()=>null,
  type:'image',
  src:imgAsset.noImg,
  className:'',
  isShow:true,
  isError:false,
  errorText:'This is important, plese fill it !',
  isDisabled:false,
  placeholder:'',
  title:null,
  validation:null,
  isFetching:false,
  objectFit:'cover',
  style:{paddingTop:'55%'},
  dimention:680,
};
export {InputImage};
