import * as types from '../types'
import {PostRequestServer} from 'helper';
import {BIODATA} from 'config/api';

export const GetBiodata= ({token},callback) => {
  let formData = new FormData();
  PostRequestServer({
      apiUrl    :BIODATA.apiUrl,
      baseUrl   :BIODATA.baseUrl,
      token     :token,
      requestTo :BIODATA.requestTo.getBiodata,
      data      :formData,
  },
  (dataResponse,{isFetching,isFound,isError,onLoading})=>{
    const {data} = dataResponse;
    callback({
      data,
      isFetching,
      isFound,
      isError,
      onLoading,
    });
  });
}
