import React, { Component } from 'react';
import {Link} from "react-router-dom";

const Button=(props)=>{
  return(
    <div className={`button-boxes ${props.className} ${props.usePadding?'':'no-pad'}`} >
      <div className={`fox-button fox-anim ${props.btnClass}`} onClick={props.onClick}>
        {props.children}
      </div>
    </div>
  )
}
Button.defaultProps={
  children:'Button',
  onClick:()=>null,
  className:'',
  btnClass:'',
  usePadding:true,
}

export {Button};
