import React, { Component } from 'react';
import {Link} from "react-router-dom";
import { connect } from 'react-redux';
import {actionCreators} from '_action';
import {PageContainer,ButtonRounded,Slider,Container,ModalWindow,ModalWindowLogin,LeftBarDashboard,TopBar} from 'components';
import {imgAsset} from 'helper';

import ItemBar from './ItemBar';

class ProdukListBoard extends Component {
  constructor(props) {
    super(props);
    this.state={
      leftMenu:false,
    }
  }
  componentDidMount=()=>{
    document.scrollingElement.scrollTop=0;

  }
  onTabbarClick=(value)=>{
    this.setState({activePos:value});
  }
  showModalLogin=(windowType)=>{
    this.setState({windowType},()=>{
      this.modalLogin.show();
    })
  }
  render() {
    const {state} = this;
    return (
      <PageContainer onLoginBtnClick={this.showModalLogin} activePos={3}>
        <ModalWindow ref={(ref)=>this.modalLogin=ref} className={"max-width-450"}>
          <ModalWindowLogin windowType={state.windowType}/>
        </ModalWindow>

        <TopBar onClick={()=>this.setState({leftMenu:!this.state.leftMenu})} title={"> Create Project"}/>
        <Container className={"project-container d-flex flex-wrap bg-white board"}>
          <LeftBarDashboard isOpen={this.state.leftMenu} activePos={window.location.pathname}/>
          <ItemBar />
        </Container>
      </PageContainer>
    );
  }
}

const mapStateToProps = (state) => ({
  ...state.UserLogin,
})
const mapDispatchToProps = (dispatch) => ({
  //setPrevTabbar: (tabbar) => dispatch(actionCreators.previousTabbar(tabbar)),
})

export default connect(mapStateToProps, mapDispatchToProps)(ProdukListBoard);
