import * as types from '../types'
import {PostRequestServer} from 'helper';
import {PRODUCT} from 'config/api';

export const GetProductDetail= ({token,id},callback) => {
  let formData = new FormData();
      formData.append('id',id);
  PostRequestServer({
      apiUrl    :PRODUCT.apiUrl,
      baseUrl   :PRODUCT.baseUrl,
      token     :token,
      requestTo :PRODUCT.requestTo.product_get_detail,
      data      :formData,
  },
  (dataResponse,{isFetching,isFound,isError,onLoading})=>{
    const {data} = dataResponse;
    callback({
      data,
      isFetching,
      isFound,
      isError,
      onLoading,
    });
  });
}

