export const ProdukAddList= ({token, name, overview, id_clusster, description, weight, stock, publish_type, images},callback) => {
  let formData = new FormData();
      formData.append('name',name);
      formData.append('overview',overview);
      formData.append('id_clusster',id_clusster);
      formData.append('description',description);
      formData.append('weight',weight);
      formData.append('stock',stock);
      formData.append('publish_type',publish_type);
      if(images.length>0){
        for (var i = 0; i < images.length; i++) {
          if(images[i].type==='new'){
            formData.append('images[]',images[i].src);
          }
        }
      }
  PostRequestServer({
      apiUrl    :PRODUCT.apiUrl,
      baseUrl   :PRODUCT.baseUrl,
      token     :token,
      requestTo :PRODUCT.requestTo.product_add,
      data      :formData,
  },
  (dataResponse,{isFetching,isFound,isError,onLoading})=>{
    const {data} = dataResponse;
    callback({
      data,
      isFetching,
      isFound,
      isError,
      onLoading,
    });
  });
}

