import React,{Component} from 'react';
import {imgAsset,OwnIcon} from 'helper';
import {Button,InputBox,InputSelect,InputImage} from 'components';
import {OptionBar} from '../OptionBar';

import { connect } from 'react-redux';
import {actionCreators} from '_action';

var isMounted;
class ProdukDetile extends Component{
  constructor(props){
    super(props);
    this.state={
      data:{},
      dataValid:{},
      isDisabled:true,
      category:[],
      type:[],
    }
  }
  componentWillMount=()=>{
    isMounted=true;
  }
  componentWillUnMount=()=>{
    isMounted=false;
  }
  componentDidMount=()=>{
    this.onGetClasster();
  }
  // onEdit=()=>{
  //   if(this.state.isDisabled){
  //     this.setState({isDisabled:false});
  //   }else{
  //     this.setState({isDisabled:true,userData:this.props.data});
  //   }
  // }
  onProdukName=(value,isValid)=>{
    this.setState({
      data:{...this.state.data,name:value},
      dataValid:{...this.state.dataValid,name:!isValid},
    })
  }
  onClassterChange=({item,index})=>{
    this.setState({
      data:{...this.state.data,id_classter:item.id,name_classter:item.name},
      dataValid:{...this.state.dataValid,id_classter:false},
    })
  }
  onOverview=(value,isValid)=>{
    this.setState({
      data:{...this.state.data,overview:value},
      dataValid:{...this.state.dataValid,overview:!isValid},
    })
  }
  onWeight=(value,isValid)=>{
    this.setState({
      data:{...this.state.data,weight:value},
      dataValid:{...this.state.dataValid,weight:!isValid},
    });
    console.log(value,isValid);
  }


  onGetClasster=()=>{
    this.setState({isFetching:true});
    actionCreators.GetClassterList({keywords:''},({data,isFetching,isFound,isError})=>{
      if(isMounted){
        if(isFound){
          this.setState({classter:data.data},()=>{

          })
        }else if(isError){
          setTimeout(()=>{
            this.onGetClasster();
          },5000)
        }
      }
    })
  }

  onImg=(img)=>{
    const {state,props}=this;
    if(img){
      let images =[{
            type:'new',
            src:img,
          }];
      this.setState({
        data:{...state.data,images:images},
        dataValid:{...this.state.dataValid,images:false},
      })
    }
  }
  onSaveProduk=(e)=>{
    const {state,props}=this;
    if(this.state.dataValid.images===false && this.state.dataValid.weight===false && this.state.dataValid.overview===false && this.state.dataValid.id_classter===false && this.state.dataValid.name===false && this.state.dataValid.description===false){
      actionCreators.ProdukAddList({
        token : props.userToken,
        name : state.data.name,
        id_classter : state.data.id_classter,
        overview : state.data.overview,
        weight : state.data.weight,
        description : state.data.description,
        images:state.data.images,
      },({data,isFound,isFetching,isError})=>{
        this.setState({isFetching,isError,isFound});
        // if(response.isFound){
        //   this.props.SetUserLogin({userToken:this.props.userToken,userData:response.data.data},'create');
        //   //this.onEdit();
        // }
      })
    }else{
      this.setState({
        dataValid:{
          description:this.state.data.description===(null||undefined)?true:false,
          images:this.state.data.images===(null||undefined)?true:false,
          name:this.state.data.name===(null||undefined)?true:false,
          id_classter:this.state.data.id_classter===(null||undefined)?true:false,
          overview:this.state.data.overview===(null||undefined)?true:false,
          weight:this.state.data.weight===(null||undefined)?true:false,
        },
      })
    }
  }
  render(){
    const {state,props}=this;
    return(
      <section className={"slide-right"}>
        <div className={"project-content"}>
          <div className={"user-info fox-flex-m fox-wrap fox-just-start fox-align-start"}>
            <div className={"user-input fox-flex fox-column"}>

              <div className={"user-img-container"}>
                <div className={"user-title"}>
                  <div className={"header"}>PROYEK DETILE</div>
                  <div className={"detile"}>Berikan informasi yang relevan dengan proyek team anda, dan buatlah semenarik mungkin agar investor bisa tertarik</div>
                </div>
              </div>
              <InputBox
                type={'text'}
                validation={'name'}
                title={"NAMA PRODUK"}
                onChange={this.onProdukName}
                value={state.data.name}
                placeholder={"Nama proyek anda"}
                isError={state.dataValid.name}
                errorText={'Nama produk sangat penting, silahkan isi minimal 10 character'}
                // isDisabled={state.isDisabled}
              />
              <InputSelect
                type={'text'}
                validation={'null'}
                title={"KATEGORI PRODUK"}
                onChange={this.onClassterChange}
                value={state.data.name_classter}
                placeholder={"Pilih kategori"}
                isError={state.dataValid.id_classter}
                errorText={'Silahkan pilih kategori produk, karena ini sangat dibutuhkan untuk membedakan jenis setiap produk'}
                data={state.classter}
                render={({item,index})=>{
                  return item.name
                }}
                // isDisabled={state.isDisabled}
              />
              <InputBox
                type={'text'}
                validation={'null'}
                title={"BERAT PRODUK"}
                onChange={this.onWeight}
                value={state.data.weight}
                placeholder={"Berapa berat produk anda, bisa dalam Kg, Ml, L dll"}
                isError={state.dataValid.weight}
                errorText ={'ini sangat dibutuhkan untuk memberikan informasi pada client '}
                minLength={1}
                maxLength={100}
                // isDisabled={state.isDisabled}
              />
              <InputBox
                type={'textarea'}
                validation={'null'}
                title={"PRODUK OVERVIEW"}
                onChange={this.onOverview}
                value={state.data.overview}
                placeholder={"Jelaskan dengan singkat inti produk anda, minimal 100 karakter"}
                isError={state.dataValid.overview}
                errorText ={'ini sangat dibutuhkan untuk menarik client agar melihat produk anda'}
                minLength={100}
                maxLength={400}
                // isDisabled={state.isDisabled}
              />
              <InputImage
                type={'image'}
                validation={'null'}
                title={"PRODUK IMAGE"}
                onChange={this.onImg}
                src={state.data.images?state.data.images[0].src:imgAsset.noImg}
                placeholder={"Buatlah gambar produk anda semenarik mungkin agar client tertarik dengan proyek anda. "}
                isError={state.dataValid.images}
                errorText={'Gambar produk sangat dibutuhkan untuk menampilkan produk anda di daftar produk'}
                // isDisabled={state.isDisabled}
              />

              <div style={{}} className={"fox-flex fox-just-end"}>
                <Button btnClass={"btn-primary"} onClick={this.onSaveProduk}>SIMPAN PRODUK</Button>
              </div>
            </div>
          </div>
        </div>
      </section>
    )
  }
}
ProdukDetile.defaultProps={
  data:{},
  children:null,
}
const mapStateToProps = (state) => ({
  ...state.UserLogin,
})
const mapDispatchToProps = (dispatch) => ({
  //setPrevTabbar: (tabbar) => dispatch(actionCreators.previousTabbar(tabbar)),
})

export default connect(mapStateToProps, mapDispatchToProps)(ProdukDetile);
