import React,{Component} from 'react';
import {OwnIcon,imgAsset,ResizeImage} from 'helper';

class TextEditor extends Component {
  constructor(props){
    super(props);
    this.state={
      isShow:false,
      isHide:false,
      value:props.children,

      contextMenu:{
        mouseX:0,
        mouseY:0,
        isShow:false,
      }
    }
  }
  componentDidMount=()=>{
    this.editor.addEventListener("contextmenu",this.onMouseRigthClick);
  }
  componentWillUnmount=()=>{
    this.editor.removeEventListener("contextmenu",this.onMouseRigthClick);
  }
  onMouseRigthClick=(e)=>{
    e.preventDefault();
    this.setState({
      contextMenu:{
        mouseX:(window.screen.width - e.clientX)>=200?e.clientX:(window.screen.width - 200),
        mouseY:(window.screen.height - e.clientY)>=500?e.clientY:(window.screen.height - 500),
        isShow:true,
      }
    })
  }
  onEditorClicked=(e)=>{
    this.dismissMenu();
  }
  dismissMenu=()=>{
    this.setState({
      contextMenu:{
        mouseX:0,
        mouseY:0,
        isShow:false,
      }
    })
  }
  onStrike=(e)=>{
    e.preventDefault();
    document.execCommand('strikeThrough',false,'');
    this.dismissMenu();
  }
  onCut=(e)=>{
    e.preventDefault();
    document.execCommand('cut',false,'');
    this.dismissMenu();
  }
  onCopy=(e)=>{
    e.preventDefault();
    document.execCommand('copy',false,'');
    this.dismissMenu();
  }
  onPaste=(e)=>{
    e.preventDefault();
    document.execCommand('paste',false,'');
    this.dismissMenu();
  }

  onUndo=(e)=>{
    e.preventDefault();
    document.execCommand('undo',false,'');
    this.dismissMenu();
  }
  onRedo=(e)=>{
    e.preventDefault();
    document.execCommand('redo',false,'');
    this.dismissMenu();
  }
  onBold=(e)=>{
    e.preventDefault();
    document.execCommand('bold',false,'');
    this.dismissMenu();
  }
  onJustify=(e)=>{
    e.preventDefault();
    document.execCommand('justifyFull',false,'');
    this.dismissMenu();
  }
  onCenter=(e)=>{
    e.preventDefault();
    document.execCommand('justifyCenter',false,'');
    this.dismissMenu();
  }
  onLeft=(e)=>{
    e.preventDefault();
    document.execCommand('justifyLeft',false,'');
    this.dismissMenu();
  }
  onRight=(e)=>{
    e.preventDefault();
    document.execCommand('justifyRight',false,'');
    this.dismissMenu();
  }
  onUnderline=(e)=>{
    e.preventDefault();
    document.execCommand('underline',false,'');
    this.dismissMenu();
  }
  onItalic=(e)=>{
    e.preventDefault();
    document.execCommand('italic',false,'');
    this.dismissMenu();
  }

  onImage=(e)=>{
    e.stopPropagation();
    e.preventDefault();
    this.setState({isLoading:true});
    this.handleResizeImage(e.target.files,0,e.target.files.length);
    this.dismissMenu();
  }
  handleResizeImage=(files,i,dataLenght)=>{
    ResizeImage(files[i],680,(src)=>{
      if(src){
        const img = document.createElement("img");
              img.src = src;
              this.editor.appendChild(img);
              i++;
              if(i===(dataLenght)){
                this.setState({isLoading:false});
              }else{
                this.handleResizeImage(files,i,dataLenght);
              }
      }
    });
  }

  onChange=(e)=>{
    this.props.onChange(e.currentTarget.innerHTML);
  }

  render(){
    const {props,state} = this;
    return (
      <div className={"fox-editor"} >
        <TopBarMenu props={this} />

        <ContextMenu props={this} contextMenu={state.contextMenu} />
        {state.isLoading &&
          <div style={{position: 'absolute'}}>
            LOADIM
          </div>
        }
        <div id={'editor'} ref={(ref)=>this.editor=ref} contentEditable={props.isEdit} className={"editor"} onInput = {this.onChange} onClick={this.onEditorClicked}>
          {props.children}
        </div>
      </div>
    )
  }
}
TextEditor.defaultProps={
  children:null, // props type react-element
  className:'',
  onChange:()=>null,
  isEdit:false,
}
export default TextEditor;

const TopBarMenu =({props})=>{
  return(
    <div className={"toolbar fox-flex fox-just-start fox-wrap"}>
      <div className={"btn"} onMouseDown={props.onUndo}>
        <OwnIcon.IconUndo className={"main-icon"} />
      </div>
      <div className={"btn"} onMouseDown={props.onRedo}>
        <OwnIcon.IconRedo className={"main-icon"} />
      </div>
      <div className={"btn"} onMouseDown={props.onBold}>
        <OwnIcon.IconBold className={"main-icon"} />
      </div>
      <div className={"btn"} onMouseDown={props.onUnderline}>
        <OwnIcon.IconUnderline className={"main-icon"} />
      </div>
      <div className={"btn"} onMouseDown={props.onItalic}>
        <OwnIcon.IconItalic className={"main-icon"} />
      </div>
      <div className={"btn"} onMouseDown={props.onStrike}>
        <OwnIcon.IconStrike className={"main-icon"} />
      </div>
      <div className={"btn"} onMouseDown={props.onCopy}>
        <OwnIcon.IconCopy className={"main-icon"} />
      </div>
      <div className={"btn"} onMouseDown={props.onPaste}>
        <OwnIcon.IconPaste className={"main-icon"} />
      </div>
      <div className={"btn"} onMouseDown={props.onCut}>
        <OwnIcon.IconCut className={"main-icon"} />
      </div>

      <div className={"btn"} onMouseDown={props.onLeft}>
        <OwnIcon.IconLeft className={"main-icon"} />
      </div>
      <div className={"btn"} onMouseDown={props.onRight}>
        <OwnIcon.IconRight className={"main-icon"} />
      </div>
      <div className={"btn"} onMouseDown={props.onCenter}>
        <OwnIcon.IconCenter className={"main-icon"} />
      </div>
      <div className={"btn"} onMouseDown={props.onJustify}>
        <OwnIcon.IconJustify className={"main-icon"} />
      </div>

      <label className={"btn"} htmlFor="file">
        <input type="file" accept="image/*" id="file" className={"hide-input"} multiple={true} onChange={props.onImage}/>
        <OwnIcon.IconImage className={"main-icon"} />
      </label>
    </div>
  )
}

const ContextMenu =({props,contextMenu})=>{
  if(contextMenu.isShow){
    return(
      <div className={"toolbar fox-flex fox-just-start fox-column context-menu"} style={{position: 'fixed',left: contextMenu.mouseX,top: contextMenu.mouseY}}>
        <div className={"btn fox-flex fox-just-start fox-align-center"} onMouseDown={props.onUndo}>
          <OwnIcon.IconUndo className={"main-icon"} />
          <i>Undo</i>
        </div>
        <div className={"btn fox-flex fox-just-start fox-align-center"} onMouseDown={props.onRedo}>
          <OwnIcon.IconRedo className={"main-icon"} />
          <i>Redo</i>
        </div>
        <div className={"btn fox-flex fox-just-start fox-align-center"} onMouseDown={props.onBold}>
          <OwnIcon.IconBold className={"main-icon"} />
          <i>Bold</i>
        </div>
        <div className={"btn fox-flex fox-just-start fox-align-center"} onMouseDown={props.onUnderline}>
          <OwnIcon.IconUnderline className={"main-icon"} />
          <i>Underline</i>
        </div>
        <div className={"btn fox-flex fox-just-start fox-align-center"} onMouseDown={props.onItalic}>
          <OwnIcon.IconItalic className={"main-icon"} />
          <i>Italic</i>
        </div>
        <div className={"btn fox-flex fox-just-start fox-align-center"} onMouseDown={props.onStrike}>
          <OwnIcon.IconStrike className={"main-icon"} />
          <i>Strike</i>
        </div>
        <div className={"btn fox-flex fox-just-start fox-align-center"} onMouseDown={props.onCopy}>
          <OwnIcon.IconCopy className={"main-icon"} />
          <i>Copy</i>
        </div>
        <div className={"btn fox-flex fox-just-start fox-align-center"} onMouseDown={props.onPaste}>
          <OwnIcon.IconPaste className={"main-icon"} />
          <i>Paste</i>
        </div>
        <div className={"btn fox-flex fox-just-start fox-align-center"} onMouseDown={props.onCut}>
          <OwnIcon.IconCut className={"main-icon"} />
          <i>Cut</i>
        </div>

        <div className={"btn fox-flex fox-just-start fox-align-center"} onMouseDown={props.onLeft}>
          <OwnIcon.IconLeft className={"main-icon"} />
          <i>Justify Left</i>
        </div>
        <div className={"btn fox-flex fox-just-start fox-align-center"} onMouseDown={props.onRight}>
          <OwnIcon.IconRight className={"main-icon"} />
          <i>Justify Right</i>
        </div>
        <div className={"btn fox-flex fox-just-start fox-align-center"} onMouseDown={props.onCenter}>
          <OwnIcon.IconCenter className={"main-icon"} />
          <i>Justify Center</i>
        </div>
        <div className={"btn fox-flex fox-just-start fox-align-center"} onMouseDown={props.onJustify}>
          <OwnIcon.IconJustify className={"main-icon"} />
          <i>Justify Full</i>
        </div>

        <label className={"btn fox-flex fox-just-start fox-align-center"} htmlFor="file">
          <input type="file" accept="image/*" id="file" className={"hide-input"} multiple={true} onChange={props.onImage}/>
          <OwnIcon.IconImage className={"main-icon"} />
          <i>Multiple Image</i>
        </label>
      </div>
    )
  }else{return null}
}
