import * as actionType from '_action/types';
const initialState={
  userToken:null,
  userData:{}
}
const SetUserLogin = (state = initialState, action) => {
    switch (action.type) {
    case actionType.SET_USER_TOKEN: {
        return {
            ...state,
            userToken:action.userToken,
            userData :action.userData,
        }
    }
    case actionType.DEL_USER_TOKEN: {
        return {
            ...state,
            userToken:null,
            userData :{},
        }
    }
    default: return state;
    }
}
export {SetUserLogin}
