import React,{Component} from 'react';
import {OwnIcon} from 'helper';

class ModalWindow extends Component {
  constructor(props){
    super(props);
    this.state={
      isShow:false,
      isHide:false,
    }
  }
  // hide modal window
  dismiss=(isOnClosed)=>{
    this.setState({isHide:true},()=>{
      setTimeout(()=>{
        this.setState({isShow:false});
        if(isOnClosed===true){
          this.props.onClosed(true);
        }
      },400);
    })
  }
  dismissParent=(event)=>{
    event.preventDefault();
    if(event.target===event.currentTarget) {
      this.dismiss();
    }
  }
  // show the modal window
  show=()=>{
    this.setState({isHide:false,isShow:true});
  }
  render(){
    const {props,state} = this;
    if(state.isShow){
      return (
        <div className={"modal-window"} >
          <div className={"modal-box"} onClick={this.dismissParent}>
            <div className={`modal-content ${!state.isHide?'':'modal-hide'} ${props.className}`}>
              { props.isHeader &&
                <div>
                  <OwnIcon.IconClose className={"modal-btn-close"} onClick={this.dismiss}/>
                </div>
              }
              {props.children}
            </div>
          </div>
        </div>
      )
    }else{
      return null
    }
  }
}
ModalWindow.defaultProps={
  children:null, // props type react-element
  onClosed:()=>null, // props type function callback
  isHeader:true, // props type bool
  className:'',
}
export default ModalWindow;
