const Regex = {
    email:/^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
    number:/([0-9])+$/,
    name:/([a-zA-Z '])+$/,
    userName:/^([a-zA-Z._'])+$/,
    spaceName:/([a-zA-Z])/,
    spaceNumber:/([0-9])/,
    spaceNull:/([a-zA-Z])/,
    password:/^(?=.*[$@#!%*?&., {}()])(?=.*[a-z])(?=.*\d)[A-Za-z\d$@#!%*?&., {}()]{6,}$/,
    null:/([^\s])/,
    money:/[0-9]/g,
    youtube:/^.*(youtu\.be\/|vi?\/|u\/\w\/|embed\/|\?vi?=|\&vi?=)([^#\&\?]*).*/,
}

//export {Regex};

const TestInput = {
    inputName:(value,minLength,callback)=>{
        value = value!==null?value:'';
        if(Regex.name.test(value) && Regex.spaceName.test(value) && value.length>=minLength){
            callback(true)
        }else{
            callback(false)
        }
    },
    inputUserName:(value,minLength,callback)=>{
        value = value!==null?value:'';
        if(Regex.userName.test(value) && Regex.spaceName.test(value) && value.length>=minLength){
            callback(true)
        }else{
            callback(false)
        }
    },
    //exp://76-6zc.myexpouser.Whybuy.exp.direct:80
    inputNumber:(value,minLength,callback)=>{
        value = value!==null?value:'';
        if(Regex.number.test(value) && Regex.spaceNumber.test(value) && value.length>=minLength){
            callback(true)
        }else{
            callback(false)
        }
    },
    inputEmail:(value,minLength,callback)=>{
        value = value!==null?value:'';
        if(Regex.email.test(value)){
            callback(true)
        }else{
            callback(false)
        }
    },
    inputPassword:(value,minLength,callback)=>{
        value = value!==null?value:'';
        if(value.length >=minLength){
            callback(true)
        }else{
            callback(false)
        }
    },
    inputRePassword:(value,acuan,minLength,callback)=>{
        value = value!==null?value:'';
        acuan = acuan!==null?acuan:'#%$#%$';
        if(acuan===value){
            callback(true)
        }else{
            callback(false)
        }
    },
    inputNull:(value,minLength,callback)=>{
        value = value!==null?value:'';
        if(Regex.null.test(value) && value.length>=minLength){
            callback(true)
        }else{
            callback(false)
        }
    },
    inputMoney:(value,minLength,callback)=>{
        value = value!==null?value:'';
        value = convertToNumber(value);
        if(Regex.number.test(value)){
            callback(true)
        }else{
            callback(false)
        }
        console.log(Regex.number.test(value));
    },
    inputYoutube:(value,callback)=>{
        value = value!==null?value:'';
        let parsed = value.match(Regex.youtube);
        if(parsed && parsed[2]){
            callback({result:true,value:parsed[2]})
        }else{
            callback({result:false,value:value})
        }
    },
    test:(value)=>{
        var data = value.replace(/[0-9]/gi,"_");
        var hasil = data.replace(/_/gi,"");
        return hasil
    }
}

const convertToRupiah=(value,)=>{
  value=value?value:'';
  //value = value.replace('.')
  value = value.toString().replace(/\D+/g,"")
	var rupiah = '';
	var angkarev = value.toString().split('').reverse().join('');
	for(var i = 0; i < angkarev.length; i++) if(i%3 == 0) rupiah += angkarev.substr(i,3)+'.';
  var result = rupiah.split('',rupiah.length-1).reverse().join('');
  //let comma = tempData[1]?tempData[1]:'00';
  //result = result!==''?result+','+comma:'';
	return result;
}
