import React, { Component } from 'react';
import {Link} from "react-router-dom";
import {imgAsset} from "helper";
import Slider from 'components/Slider';

class ListingItem extends Component{
  constructor(props) {
    super(props);
    this.state={}
  }
  shouldComponentUpdate(nextProps, nextState) {
    if (this.props.item === nextProps.item) {
      return false;
    } else {
      return true;
    }
  }
  render(){
    const {props} = this;
    let img = imgAsset.noImg;
        if(props.item.images.length>0){
          img = props.item.images[0].file_link;
        }
    return(
      <Link to={props.to} className={`list-item fox-mb-15 item ${props.className}`}>
        <div className={`list-item-box fox-flex fox-column fox-just-space`} onClick={props.onClick}>
          <div className={"list-item-img"}>
            <img src={img} />
          </div>

          <div className={"list-item-content d-flex flex-column justify-content-between"}>
            <div className={"title"}>{props.item.name}</div>
            <div  className={"sub-title"}>{props.item.overview}</div>
            <div className={"d-flex justify-content-between align-items-center price-box"}>
              <div className={"title no-margin"}>Isi Bersih</div>
              <div className={"title no-margin price"}>{props.item.weight}</div>
            </div>
          </div>
        </div>
      </Link>
    )
  }
}
ListingItem.defaultProps={
  onClick:()=>null,
  className:'',
  item:{},
  index:0,
  to:'/project-invention-now/1/view/'
}

export default ListingItem;
