import React,{Component} from 'react';
import {imgAsset,OwnIcon} from 'helper';
import {Button,ListingItem,FlatList,LoaderProjectItem} from 'components';
import {OptionBar} from '../OptionBar';

import { connect } from 'react-redux';
import {actionCreators} from '_action';

let isMounted;
class ItemBar extends Component{
  constructor(props){
    super(props);
    this.state={
      dataListing:[],
    }
  }
  componentWillMount=()=>{
    isMounted=true;
  }
  componentWillUnMount=()=>{
    isMounted=false;
  }
  componentDidMount=()=>{
    this.onGetListing();
  }
  onGetListing=()=>{
    actionCreators.GetProductList({token:this.props.userToken,keywords:''},({data,isFetching,isFound,isError})=>{
      this.setState({isFound,isFetching,isError});
      if(isMounted){
        if(isFound){
          this.setState({dataListing:data.data,isFetching});
        }else if(isError){
          this.onGetListing();
        }
      }
    })
  }
  render(){
    const {state,props}=this;
    return(
      <section className={"item-bar"} >
        <section className={"slide-right"}>
          <div className={"project-content"}>
            <div className={"project-header fox-flex fox-just-start fox-align-center"}>
              {/**<label className={"header-text"}>Sort by</label>**/}

            </div>
            <div className={"project-content"}>
              <FlatList className={"fox-row fox-wrap"}  data={state.isFound?state.dataListing:[]} render={({item,index})=>{
                return <ListingItem key={index.toString()} className={"fox-col-3-m"} item={item} index={index} to={{pathname:`/board-create-produk/${item.id}/edit`}}/>
              }} />
              <LoaderProjectItem isFetching={state.isFetching}/>
            </div>
          </div>
        </section>
      </section>
    )
  }
}
ItemBar.defaultProps={
  children:null,
}
const mapStateToProps = (state) => ({
  ...state.UserLogin,
})
const mapDispatchToProps = (dispatch) => ({
  //setPrevTabbar: (tabbar) => dispatch(actionCreators.previousTabbar(tabbar)),
})

export default connect(mapStateToProps, mapDispatchToProps)(ItemBar);
