import React, { Component } from 'react';
import {Link} from "react-router-dom";
import {imgAsset,OwnIcon} from 'helper';

const LinkComponent=(props)=>{
  return(
    <ul className={`fox-flex fox-row fox-align-end fox-btn-box-m header-m fox-anim ${props.isOpen?'nav-show':''}`}>
      {
        props.data.map((item,index)=>{
          if(item.onClick){
            if(props.isLoginPage && item.text==='Register'){
              return null
            }else{
              return(
                <li onClick={()=>item.onClick(item.link)} className={index===props.activePos?"active":"noActive fox-anim"} key={index.toString()}>
                  <label className={"fox-flex fox-row fox-just-space fox-align-center fox-v-100"}>
                    <div className={"fox-nav-btn c-gray"}>{item.text}</div>
                  </label>
                </li>
              )
            }
          }else{
            if(item.user){
              return(
                <li className={index===props.activePos?"active":"noActive fox-anim"} key={index.toString()}>
                  <Link to={item.link} className={"fox-flex fox-row fox-just-space fox-align-center fox-v-100"}>
                    <OwnIcon.IconUserName className={"nav-icon"} />
                    <div className={"fox-nav-btn c-gray"}>{item.text}</div>
                  </Link>
                </li>
              )
            }else{
              return(
                <li className={index===props.activePos?"active":"noActive fox-anim"} key={index.toString()}>
                  <Link to={item.link} className={"fox-flex fox-row fox-just-space fox-align-center fox-v-100"}>
                    <div className={"fox-nav-btn c-gray"}>{item.text}</div>
                  </Link>
                </li>
              )
            }
          }
        })
      }
    </ul>
  )
}

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpen:false,
      data:props.userToken===null?[
          {link:'/produk',text:'Explore Produk'},
          {link:'/tentang',text:'Tentang Kami'},
          //{link:'/layanan',text:'Layanan'},
          {link:'/kontak',text:'Hubungi kami'},
          {link:'/karir',text:'Karir'},
        ]:[
          {link:'/produk',text:'Explore Produk'},
          {link:'/tentang',text:'tentang kami'},
          //{link:'/layanan',text:'Layanan'},
          {link:'/kontak',text:'Hubungi kami'},
          {link:'/karir',text:'Karir'},
          {link:'/board',text:'MYBOARD',user:true},
        ],
    };
  }
  render() {
    return (
      <div className={"fox-header bg-white fox-box-shadow-4 fox-fixed"}>
        <div className={"fox-container fox-flex fox-row fox-just-space fox-align-center fox-v-100"}>
          <div className={"fox-logo fox-row fox-space"}>
            <Link to={"/"} className={"fox-flex fox-row fox-just-space fox-align-center fox-v-100"}>
              <img src={imgAsset.headLogo} />
              <div className={"logo-text d-flex justify-content-between align-items-end flex-column"}>
                <div className="title">PT. Mitra Buana Sari</div>
                <div className="slogan">Indonesia</div>
              </div>
            </Link>
          </div>
          <div className={"fox-navigator"}>
            <LinkComponent isOpen={this.state.isOpen} data={this.state.data} activePos={this.props.activePos} isLoginPage={this.props.isLoginPage}/>
            <ul className={"fox-btn-box-show"}>
              <li className={"fox-anim"} onClick={()=>this.setState({isOpen:!this.state.isOpen})}>
                <div className={"fox-nav-btn c-gray fox-flex fox-row fox-just-space fox-align-center fox-v-100"}>
                  {this.state.isOpen && <OwnIcon.IconClose className="nav-icon-menu" />}
                  {!this.state.isOpen && <OwnIcon.IconMenu className="nav-icon-menu" />}
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    );
  }
}
Header.defaultProps={
  activePos:null,
  isLoginPage:false,
  userToken:null,
}
export {Header};
