import React from 'react';

const LoaderElipsis = (props) => {
  if(props.isFetching){
    return (
      <div className={`elipsis-box fox-flex fox-just-center fox-align-center ${props.isAbsolute?'elipsis-absolute':''}`} style={{width: '100%'}}>
        <div className="lds-ellipsis"><div></div><div></div><div></div><div></div></div>
      </div>
    );
  }else{
    return props.children;
  }
};
LoaderElipsis.defaultProps={
  children:null,
  isFetching:false,
  isAbsolute:false,
}
export {LoaderElipsis};
