import React from 'react';
import ReactDOM from 'react-dom';

import RouterMap from './_router';
import * as serviceWorker from './serviceWorker';

import storage from 'redux-persist/lib/storage'
import { PersistGate } from 'redux-persist/integration/react';
import { Provider } from 'react-redux';
import { persistStore, persistReducer } from 'redux-persist'
import { createStore, applyMiddleware } from 'redux';
import thunkMiddleware from 'redux-thunk';
import reducers from './_reducer';

String.prototype.ucwords = function() {
    return this.toString().toLowerCase().replace(/\b[a-z]/g, function(letter) {
        return letter.toString().toUpperCase();
    });
}
function configureStore() {
    const initialState = {}
    const persistConfig = {
        key: 'root',
        storage: storage,
        whitelist: ['userToken','UserLogin'],
    }
    const persistedReducer = persistReducer(persistConfig, reducers)
    const store = createStore(persistedReducer, initialState,applyMiddleware(thunkMiddleware))
          //store.dispatch(actionCreators.GetLocalization());
    const persistedStore = persistStore(store)
    return {
        store,
        persistedStore,
    }
}

ReactDOM.render(
    <Provider store={configureStore().store}>
      <PersistGate loading={null} persistor={configureStore().persistedStore}>
        <RouterMap />
      </PersistGate>
    </Provider>
  , document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.register();
