import React, { Component } from 'react';
import {Link,withRouter} from "react-router-dom";
import { connect } from 'react-redux';
import {actionCreators} from '_action';
import {
    PageContainer,RoundedBox,Container,Banner,BannerComingSoon,
    FlatList,ListingItem,ModalWindow,ModalWindowLogin,LoaderProjectItem
} from 'components';

import {imgAsset} from 'helper'


const NewestProduct=(props)=>{
  return(
    <RoundedBox className={"fox-content bg-tail"}  style={{marginBottom: '60px'}}>
      <div className={"fox-container"}>
        <div className={"content-title"}>Produk Kami</div>
        <div className={"content-sub-title"}>A man of letters. A Platonic dreamer in a dream world. A masochist with an idealized ego that denies reality.</div>
        <div className={"content-value"}>
          <FlatList data={props.data} className={"d-flex flex-wrap"} render={({item,index})=>{
            return (
              <ListingItem className={"fox-col-3-m"} item={item} index={index} to={{pathname:`/project-invention-now/${item.id}/view`}}/>
            )
          }}/>
          <LoaderProjectItem isFetching={props.isFetching} className={"fox-col-3-m"} data={[1,2,3,4,5,6]}/>
        </div>
      </div>
    </RoundedBox>
  )
}
let isMounted;
class HomePage extends Component {
  constructor(props) {
    super(props);
    this.state={
      dataListing:[],
    }
  }
  componentWillMount=()=>{
    isMounted=true;
  }
  componentDidMount=()=>{
    document.scrollingElement.scrollTop=0;
    this.onGetListing();
    //window.location.reload('hh.com');
  }

  onGetListing=()=>{
    actionCreators.GetProductListNewest({keywords:''},({data,isFetching,isFound,isError})=>{
      this.setState({isFound,isFetching:true,isError});
      if(isMounted){
        if(isFound){
          this.setState({dataListing:data.data,isFetching})
        }else if(isError){
          setTimeout(()=>{
            this.onGetListing();
          },5000);
        }
      }
    })
  }
  render() {
    const {state,props} = this;
    return (
      <PageContainer >

        <Container>
          <Banner/>
          <BannerComingSoon />
          <NewestProduct data={state.dataListing} isFetching={state.isFetching} isFound={state.isFound} isError={state.isError}/>
        </Container>

      </PageContainer>
    );
  }
}

const mapStateToProps = (state) => ({
  ...state.UserLogin,
})
const mapDispatchToProps = (dispatch) => ({
  //setPrevTabbar: (tabbar) => dispatch(actionCreators.previousTabbar(tabbar)),
})

export default connect(mapStateToProps, mapDispatchToProps)(HomePage);
