import React, { Component } from 'react';

class FlatList extends Component{
  componentDidMount=()=> {
    //create listener scroll
    window.addEventListener('scroll', this.onScroll);
  }

  componentWillUnmount=()=> {
    //remove listener scroll when component unmount
    window.removeEventListener('scroll', this.onScroll);
  }

  onScroll=(e)=>{
    //console.log(e);
    var node = e.target.scrollingElement;
    const isBottom = node.scrollHeight - node.scrollTop <= node.clientHeight;
    //this props will be return boolean
    this.props.onScroll({isBottom});
  }
  render(){
    const {props} = this;
    return(
      <div className={`fox-flat-list fox-flex fox-wrap ${props.className}`}>
        {
          props.data.map((item,index)=>{
            return props.render({item,index});
          })
        }
      </div>
    )
  }
}
FlatList.defaultProps={
  className:'',
  data:[],
  onScroll:()=>null,
  render:()=>null,
}

export default FlatList;
