import React,{Component} from 'react';
import {imgAsset,OwnIcon} from 'helper';
import {Button,InputBox,InputImage,LoaderElipsis} from 'components';
import { connect } from 'react-redux';
import {actionCreators} from '_action';

class ItemBar extends Component{
  constructor(props){
    super(props);
    this.state={
      data:props.userData,
      dataValid:{
        name:props.userData.name===null?true:false,
        address:props.userData.address===null?undefined:false,
        card_number:props.userData.card_number===undefined?true:false,
        phone_number:props.userData.phone_number===undefined?true:false,
      },
      isDisabled:true,
    }
  }
  componentDidMount=()=>{
    console.log(this.state.dataValid);
  }
  onEdit=()=>{
    if(this.state.isDisabled){
      this.setState({isDisabled:false});
    }else{
      this.setState({isDisabled:true,data:this.props.userData});
    }
  }
  onName=(value,isValid)=>{
    this.setState({
      data:{...this.state.data,name:value},
      dataValid:{...this.state.dataValid,name:!isValid},
    })
  }
  onPhone=(value,isValid)=>{
    this.setState({
      data:{...this.state.data,phone_number:value},
      dataValid:{...this.state.dataValid,phone_number:!isValid},
    })
  }
  onAddress=(value,isValid)=>{
    this.setState({
      data:{...this.state.data,address:value},
      dataValid:{...this.state.dataValid,address:!isValid},
    })
  }
  onCardNumber=(value,isValid)=>{
    this.setState({
      data:{...this.state.data,card_number:value},
      dataValid:{...this.state.dataValid,card_number:!isValid},
    })
  }
  onImgUser=(img)=>{
    const {state,props}=this;
    if(img){
      this.setState({
        data:{...state.data,img_file:URL.createObjectURL(img)},
        //dataValid:{...this.state.dataValid,img_card_id:true},
      },()=>{
        actionCreators.AddImageUser({
          token       :props.userToken,
          img         :state.data.img,
          img_file    :img,
        },(response)=>{
          this.setState({isFetchingImgUser:response.isFetching});
          if(response.isFound){
            this.props.SetUserLogin({userToken:this.props.userToken,userData:response.data.data},'create');
          }
        })
      })
    }
  }


  onSaveBiodata=(e)=>{
    const {state,props}=this;
    if(this.state.dataValid.name===false && this.state.dataValid.address===false && this.state.dataValid.phone_number===false && this.state.dataValid.card_number===false){
      actionCreators.EditBiodata({
        token             :props.userToken,
        address           :state.data.address,
        card_number       :state.data.card_number,
        phone_number      :state.data.phone_number,
        name              :state.data.name
      },(response)=>{
        this.setState({isFetching:response.isFetching,isError:response.isError,isFound:response.isFound});
        if(response.isFound){
          this.props.SetUserLogin({userToken:this.props.userToken,userData:response.data.data},'create');
          this.onEdit();
        }
      })
    }else{
      this.setState({
        dataValid:{
          name:this.state.data.name===null?true:false,
          address:this.state.data.address===null?true:false,
          card_number:this.state.data.card_number===null?true:false,
          phone_number:this.state.data.phone_number===null?true:false,
        },
      })
    }
  }
  render(){
    const {state,props}=this;
    return(
      <section className={"item-bar"} >
        <div className={"slide-right"}>
          <LoaderElipsis isFetching={state.isFetching}>
            <div className={"project-header fox-flex fox-just-end fox-align-center"}>
              {!state.isDisabled &&
                <Button btnClass={"btn-primary"} onClick={this.onSaveBiodata}>SAVE</Button>
              }
              <Button btnClass={`btn-primary ${state.isDisabled?'':'btn-red'}`} onClick={this.onEdit}>{state.isDisabled?'EDIT':'CANCLE'}</Button>

            </div>
          </LoaderElipsis>
          <div className={"project-content"}>
            <div className={"user-info fox-flex-m fox-wrap fox-just-start fox-align-start"}>
              <div className={"alert-info fox-flex fox-align-start"}>
                <OwnIcon.IconNotification className={"icon"} />
                <label>You must complete the information in your account, before continue. a spacially for ID card, you need complete it to verify your account</label>
              </div>
              <div className={"user-input fox-flex fox-column"}>
                <div className={"user-img-container"}>
                  <div className={"user-img-box"}>
                    <InputImage
                      type={'card'}
                      validation={'null'}
                      onChange={this.onImgUser}
                      src={state.data.img_file}
                      style={{width: '100%',paddingTop: '100%'}}
                      isDisabled={state.isDisabled}
                      isFetching={state.isFetchingImgUser}
                    />
                  </div>
                </div>
                <InputBox
                  type={'text'}
                  validation={'name'}
                  onChange={this.onName}
                  value={state.data.name}
                  iconLeft={(<OwnIcon.IconUserName className={"input-icon"} />)}
                  placeholder={"Full Name"}
                  isError={state.dataValid.name && !state.isDisabled}
                  errorText={'Please insert your real name'}
                  isDisabled={state.isDisabled}
                />
                <InputBox
                  type={'email'}
                  value={state.data.email}
                  iconLeft={(<OwnIcon.IconEmail className={"input-icon"} />)}
                  placeholder={"Email"}
                  isDisabled={true}
                />
                <InputBox
                  type={'text'}
                  validation={'number'}
                  onChange={this.onPhone}
                  value={state.data.phone_number}
                  iconLeft={(<OwnIcon.IconWhatsApp className={"input-icon"} />)}
                  placeholder={"Phone Number"}
                  isError={state.dataValid.phone_number && !state.isDisabled}
                  errorText={'Please insert with correct phone number'}
                  minLength={9}
                  isDisabled={state.isDisabled}
                />
                <InputBox
                  type={'textarea'}
                  validation={'null'}
                  onChange={this.onAddress}
                  value={state.data.address}
                  iconLeft={(<OwnIcon.IconLocation className={"input-icon"} />)}
                  placeholder={"Current Address"}
                  isError={state.dataValid.address && !state.isDisabled}
                  errorText={'Please insert with correct address where you leave'}
                  minLength={9}
                  isDisabled={state.isDisabled}
                />
                <InputBox
                  type={'text'}
                  validation={'number'}
                  onChange={this.onCardNumber}
                  value={state.data.card_number}
                  iconLeft={(<OwnIcon.IconWhatsApp className={"input-icon"} />)}
                  placeholder={"ID Card Number"}
                  isError={state.dataValid.card_number && !state.isDisabled}
                  errorText={'Please insert with your id card number, its really important to verofy your account'}
                  minLength={10}
                  isDisabled={state.isDisabled}
                />

              </div>
            </div>
          </div>
        </div>
      </section>
    )
  }
}
ItemBar.defaultProps={
  children:null,
}
const mapStateToProps = (state) => ({
  ...state.UserLogin,
})
const mapDispatchToProps = (dispatch) => ({
  SetUserLogin: (data,command) => dispatch(actionCreators.SetUserLogin(data,command)),
})

export default connect(mapStateToProps, mapDispatchToProps)(ItemBar);
