import React,{Component} from 'react';
import {imgAsset,OwnIcon} from 'helper';
import {Button,InputBox,InputSelect,InputImage} from 'components';
import {OptionBar} from '../OptionBar';

import { connect } from 'react-redux';
import {actionCreators} from '_action';

import ProdukDetile from './ProdukDetile';

class ItemBar extends Component{
  constructor(props){
    super(props);
    this.state={
      activePos:1,
      data:{},
    }
  }
  onOptiobarClick=(value)=>{
    this.setState({activePos:value});
  }
  onNextButton=({data,activePos})=>{
    this.setState({data,activePos})
  }
  render(){
    const {state,props}=this;
    return(
      <section className={"item-bar"} >
        {
          //<OptionBar onClick={this.onOptiobarClick} activePos={state.activePos}/>
        }
        <ProdukDetile data={state.data}/>
      </section>
    )
  }
}
ItemBar.defaultProps={
  data:[],
  children:null,
}
const mapStateToProps = (state) => ({
  ...state.UserLogin,
})
const mapDispatchToProps = (dispatch) => ({
  //setPrevTabbar: (tabbar) => dispatch(actionCreators.previousTabbar(tabbar)),
})

export default connect(mapStateToProps, mapDispatchToProps)(ItemBar);
