import React,{Component} from 'react';
import {TestInput,OwnIcon} from 'helper';
import {LoaderElipsis} from 'components/loader';

class InputSelect extends Component{
  constructor(props){
    super(props);
    this.state={
      isShow:false,
    };
  }
  onFocus=(e)=>{
    if(e){e.preventDefault();}
    this.setState({isShow:true});
  }
  onBlur=(e)=>{
    if(e){e.preventDefault();}
    setTimeout(()=>{
      this.setState({isShow:false});
    },200);
  }
  onType=(e)=>{
    if(e){e.preventDefault();}
    this.props.onType(e.target.value,e)
  }
  onTextChage=(item,index)=>{
    this.props.onTextChage({item:item,index:index})
  }
  iconClicked=(e)=>{
    e.preventDefault();
    this.selectInput.focus()
  }
  render(){
    const {state,props}=this;
    if(props.isShow){
      if(props.isDisabled!==true){
        return(
          <div className={props.isRow?'input-parent fox-flex fox-just-space fox-align-center':'input-parent'}>
            {props.title!==null && <div className={`${props.isRow?'text-title-row':'text-title'}`}>{props.title}</div> }
            <div className={`fox-input ${props.className} ${props.isRow?'fox-col-66':''} ${props.listDataRender({item:null,index:null})!==null?'mb-2':''}`} style={props.style}>
              {props.iconLeft}
              <input type={props.type}
                className={props.title!==null?'pl-25':''}
                onFocus={this.onFocus}
                onChange={this.onType}
                onBlur={this.onBlur}
                value={props.value}
                maxLength={props.maxLength}
                //readOnly={true}
                placeholder={props.placeholder}
                ref={(ref)=>this.selectInput=ref}
              />
              {state.isShow && props.data &&
                <div className={"select-dropdown"}>
                  <LoaderElipsis isFetching={props.isFetching}>
                  {
                    props.data.map((item,index)=>{
                      return (
                        <div className={"select-item"} key={index.toString()} onClick={()=>props.onChange({item,index})} key={index.toString()}>
                          {props.render({item,index})}
                        </div>
                      )
                    })
                  }
                  </LoaderElipsis>
                </div>
              }
              <OwnIcon.IconDropDown className={"drop-icon"} onClick={this.iconClicked}/>
              {props.isError===true && <label className={"error"}>{props.errorText}</label> }
            </div>
            {props.listDataRender({item:null,index:null})!==null &&
              <div className={"listing-member"}>
                {props.listData.map((item,index)=>{
                  return (
                    <div className={"listing-member-item fox-flex fox-align-center"} key={index.toString()}>
                      {props.listDataRender({item,index})}
                    </div>
                  );
                })}
              </div>
            }
          </div>
        )
      }else{
        return(
          <div className={props.isRow?'input-parent fox-flex fox-just-space fox-align-center':'input-parent'}>
            {props.title!==null && <div className={`${props.isRow?'text-title-row':'text-title'}`}>{props.title}</div> }
            <div className={`fox-input ${props.className} ${props.isRow?'fox-col-66':''} ${props.listDataRender({item:null,index:null})!==null?'bt-1 mb-2':''}`} style={props.style}>
              {props.iconLeft}
              {props.listDataRender({item:null,index:null})===null &&
                <div className={`input-deactive ${props.title!==null?'pl-25':''}`}>{(props.value!=='' && props.value!==null && props.value)?props.value:props.placeholder }</div>
              }
              {props.iconRigth}
              {props.isError===true && <label className={"error"}>{props.errorText}</label> }
            </div>
            {props.listDataRender({item:null,index:null})!==null &&
              <div className={"listing-member"}>
                {props.listData.map((item,index)=>{
                  return (
                    <div className={"listing-member-item fox-flex fox-align-center"} key={index.toString()}>
                      {props.listDataRender({item,index})}
                    </div>
                  );
                })}
              </div>
            }

          </div>
        )
      }
    }else{
      return null;
    }
  }
}
InputSelect.defaultProps={
  onChange:()=>null,
  onType:()=>null,
  onFocus:()=>null,
  onBlur:()=>null,
  type:'text',
  value:undefined,
  className:'',
  isShow:true,
  isError:false,
  errorText:'This is important, plese fill it !',
  isDisabled:false,
  iconLeft:null,
  placeholder:'',
  title:null,
  isRow:false,
  validation:null,
  data:undefined,
  render:()=>null,
  listData:[],
  listDataRender:()=>null,
};
export {InputSelect};
