import * as types from '../types'
import {PostRequestServer} from 'helper';
import {CLASSTER} from 'config/api';

export const GetClassterDetail= ({token,id},callback) => {
  let formData = new FormData();
      formData.append('id',id);
  PostRequestServer({
      apiUrl    :CLASSTER.apiUrl,
      baseUrl   :CLASSTER.baseUrl,
      token     :token,
      requestTo :CLASSTER.requestTo.classter_get_detail,
      data      :formData,
  },
  (dataResponse,{isFetching,isFound,isError,onLoading})=>{
    const {data} = dataResponse;
    callback({
      data,
      isFetching,
      isFound,
      isError,
      onLoading,
    });
  });
}

