import React, { Component } from 'react';
import {Link} from "react-router-dom";
import {imgAsset} from 'helper';

const FooterTopChild=({data,activePos})=>{
  return(
    <div className={"col-md-3 col-sm-3 fox-mb-15"}>
      <div className={"foot-title"}>{data.title}</div>
      <ul className={"fox-flex fox-column fox-align-start"} >
        {
          data.childData.map((item,index)=>{
            if(item.type==="link"){
              return(
                <li className={index===activePos?"active":"noActive fox-anim"} key={index.toString()} >
                  <a href={item.link} target="_blank" className={"fox-flex fox-wrap fox-row fox-just-space fox-align-center fox-v-100"}>
                    <div className={"fox-foot-btn c-white"}>{item.text}</div>
                  </a>
                </li>
              )
            }else{
              return(
                <li className={index===activePos?"active":"noActive fox-anim"} key={index.toString()} >
                  <Link to={item.link} className={"fox-flex fox-wrap fox-row fox-just-space fox-align-center fox-v-100"}>
                    <div className={"fox-foot-btn c-white"}>{item.text}</div>
                  </Link>
                </li>
              )
            }
          })
        }
      </ul>
    </div>
  )
}
const FooterTop=({data,activePos})=>{
  return(
    <div className={"fox-container"}>
      <div className={"fox-footer-top d-flex justify-content-between flex-wrap fox-v-100"}>
        <div className={"col-md-3 col-sm-3 fox-mb-15"}>
          <div className={"foot-img"}>
            <img src={imgAsset.headLogoWhite} />
            <div className="foot-img-text">PT. Mitra Buana Sari</div>
          </div>
        </div>
        {
          data.map((item,index)=>{
            return(
              <FooterTopChild data={item} key={index.toString()} />
            )
          })
        }
      </div>
    </div>
  )
}

const FooterBottom=({data,activePos})=>{
  return(
    <div className={"fox-container"}>
      <div className={"fox-footer-bottom fox-flex fox-wrap fox-row fox-just-space-m fox-align-center fox-v-100"}>
        <ul className={"fox-flex fox-row fox-align-start fox-mb-15"}>
          {
            data.map((item,index)=>{
              return(
                <li className={index===activePos?"active":"noActive fox-anim"} key={index.toString()} >
                  <Link to={item.link} className={"fox-flex fox-row fox-just-space fox-align-center fox-v-100"}>
                    <div className={"fox-foot-btn c-white"}>{item.text}</div>
                  </Link>
                </li>
              )
            })
          }
        </ul>
        <div className={"fox-foot-btn c-white copy"}>&copy; 2019 PT. Mitra Buana Sari Indonesia, Inc. All Rights Reserved</div>
      </div>
    </div>
  )
}

class Footer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dataFooterTop:[
        { title:'SITEMAP',
          childData:[
            {link:'/produk',text:'Explore Produk'},
            {link:'/tentang',text:'Tentang Kami'},
            //{link:'/layanan',text:'Layanan'},
            {link:'/kontak',text:'Hubungi kami'},
            {link:'/karir',text:'Karir'},
        ]},
        { title:'KONTAK',
          childData:[
            {link:'/kontak',text:'Kontak & Support'},
            {link:'/faqs',text:'FAQS'},
        ]},
        { title:'MEDIA SOSIAL',
          childData:[
            {link:'https://www.facebook.com',text:'Facebook',type:'link'},
            {link:'https://www.twitter.com',text:'Twitter',type:'link'},
            {link:'https://www.instagram.com',text:'Instagram',type:'link'},
        ]},
      ],
      dataFooterBottom:[
        {link:'/#',text:'Terms Of Use'},
        {link:'/#',text:'Privacy Policy'},
        {link:'/#',text:'Trust and Safty'},
      ]
    };
  }
  render() {
    return (
      <div className={"bg-red"}>
        <div className={"fox-footer"}>
          <FooterTop data={this.state.dataFooterTop} />
        </div>
        <div className={"bg-red-dark"}>
          <div className={"fox-footer"}>
            <FooterBottom data={this.state.dataFooterBottom}/>
          </div>
        </div>
      </div>
    );
  }
}
Footer.defaultProps={

}
export {Footer};
