import React from 'react';
import {TestInput} from 'helper'

const InputBox =(props)=> {
  const onChange=(event)=>{
    event.stopPropagation();
    event.preventDefault();
    if(props.onChange){
      if(props.validation!==null){
        if(props.validation==='name'){
          TestInput.inputName(event.target.value,props.minLength,(valid)=>{
            props.onChange(event.target.value,valid);
          })
        }else if(props.validation==='email'){
          TestInput.inputEmail(event.target.value,props.minLength,(valid)=>{
            props.onChange(event.target.value,valid);
          })
        }else if(props.validation==='null'){
          TestInput.inputNull(event.target.value,props.minLength,(valid)=>{
            props.onChange(event.target.value,valid);
          })
        }else if(props.validation==='number'){
          TestInput.inputNumber(event.target.value,props.minLength,(valid)=>{
            props.onChange(event.target.value,valid);
          })
        }
      }else{
        props.onChange(event.target.value,true);
      }
    }
  }
  if(props.isShow){
    if(props.isDisabled!==true){
      return(
        <div className={props.isRow?'input-parent fox-flex fox-just-space fox-align-center':'input-parent'}>
          {props.title!==null && <div className={`${props.isRow?'text-title-row':'text-title'}`}>{props.title}</div> }
          <div className={`fox-input ${props.className} ${props.isRow?'fox-col-66':''}`} style={props.style}>
            {props.iconLeft}
            {props.type==='textarea'?(
              <textarea
                className={props.title!==null?'pl-25':''}
                onFocus={props.onFocus}
                onChange={onChange}
                onBlur={props.onTextBlur}
                maxLength={props.maxLength}
                placeholder={props.placeholder}
                rows={5}
                value={props.value}
              ></textarea>
            ):(
              <input type={props.type}
                className={props.title!==null?'pl-25':''}
                onFocus={props.onFocus}
                onChange={onChange}
                onBlur={props.onTextBlur}
                value={props.value===null?'':props.value}
                maxLength={props.maxLength}
                placeholder={props.placeholder}
              />
            )}
            {props.iconRigth}
            {props.isError===true && <label className={"error"}>{props.errorText}</label> }
          </div>
        </div>
      )
    }else{
      return(
        <div className={props.isRow?'input-parent fox-flex fox-just-space fox-align-center':'input-parent'}>
          {props.title!==null && <div className={`${props.isRow?'text-title-row':'text-title'}`}>{props.title}</div> }
          <div className={`fox-input ${props.className} ${props.isRow?'fox-col-66':''}`} style={props.style}>
            {props.iconLeft}
            <div className={`input-deactive ${props.title!==null?'pl-25':''}`}>{(props.value!=='' && props.value!==null && props.value)?props.value:props.placeholder }</div>
            {props.iconRigth}
            {props.isError===true && <label className={"error"}>{props.errorText}</label> }
          </div>
        </div>
      )
    }
  }else{
    return null;
  }
}
InputBox.defaultProps={
  onChange:()=>null,
  onFocus:()=>null,
  onBlur:()=>null,
  type:'text',
  value:undefined,
  className:'',
  isShow:true,
  isError:false,
  errorText:'This is important, plese fill it !',
  isDisabled:false,
  iconLeft:null,
  iconRigth:null,
  placeholder:'',
  title:null,
  isRow:false,
  minLength:0,
  maxLength:null,
  validation:null,
};
export {InputBox};
