import React,{Component} from 'react';
import {withRouter,Redirect} from 'react-router-dom';

import { GoogleLogin } from 'react-google-login';
import { googleClientId } from 'config/api';
import {OwnIcon} from 'helper';
import {Button} from 'components/Button';
import {InputBox} from 'components/Input';
import {LoaderElipsis} from 'components/loader';
import {actionCreators} from '_action';
import { connect } from 'react-redux';

const ModalTitle=(props)=>{
  if(props.windowType==='LOGIN_MODAL'){
    return(
      <label className={"modal-titles"}>Hi, welcome back to <strong>Indcrowd</strong></label>
    )
  }else if(props.windowType==='REGISTER_MODAL'){
    return (
      <label className={"modal-titles"}>Hi, Welcome to <strong>Indcrowd</strong></label>
    )
  }else if(props.windowType==='LOGIN_PAGE'){
    return (
      <label className={"modal-titles"}>Before you continue using <strong>Indcrowd</strong>, you must <strong>Sign-in</strong> first. please fill the box bellow</label>
    )
  }
}
const RegisterInfo=(props)=>{
  if(props.windowType!=='REGISTER_MODAL'){
    return (
      <label className={"fox-mb-15 fox-center fox-block text-info"}>You can <strong>Sign-in</strong> and <strong>Register</strong> using <strong>Google Account</strong></label>
    )
  }else{
    return(
      <label className={"fox-mb-15 fox-center fox-block text-info"}><strong>Incrowd</strong> is connected to the <strong>Google Account</strong>. To continue with <strong>Indcrowd</strong> please make google account firts, and then click button bellow</label>
    )
  }
}

class ModalWindowLogin extends Component {
  constructor(props){
    super(props);
    this.state={
      isFetching:false,
      isError:false,
      email:'',
      password:'',
    }
  }
  getBiodata=(token)=>{
    actionCreators.GetBiodata({token},({data,isFetching,isFound,isError,onLoading})=>{
      this.setState({isFound,isFetching,isError});
      if(isFound){
        this.props.SetUserLogin({userToken:token,userData:data.data},'create');

      }else if(isError){
        this.setState({
          isError,isFetching,isFound,errorMessage:data.message,
        })
      }
    })
  }
  handleSignin = (response) => {
    const { accessToken,googleId,profileObj } = response;
    if (accessToken){
      actionCreators.UserLogin({
        name        :profileObj.name,
        token_medsos:profileObj.googleId,
        img         :profileObj.imageUrl,
        type        :'google',
        email       :profileObj.email,
      },({data,isFetching,isFound,isError,onLoading})=>{
        if(isFound){
          //get
        }else if(isError){
          this.setState({
            isError,isFetching,isFound,errorMessage:data.message,
          })
        }
      })
    }
  }
  onLoginManual=()=>{
    let {state } = this;
    actionCreators.UserLogin({
      email : state.email,
      password : state.password,
    },({data,isFetching,isFound,isError,onLoading})=>{
      if(isFound){
        this.props.SetUserLogin({userToken:data.data.token,userData:data.data},'create');
        if(window.location.pathname!=='/sign-in'){
          this.props.history.push(window.location.patname);
        }else{
          this.props.history.push('/board');
        }
          console.log(data.data,state.email,state.password,data);
      }else if(isError){
        this.setState({
          isError,isFetching,isFound,errorMessage:data.message,
        })
      }
    })
  }
  render(){
    const {props,state} = this;
    if(props.userToken!==null){
      return (<Redirect to={props.redirectTo}/>)
    }else{
      return(
      <div className={"fox-form"}>
        <ModalTitle windowType={props.windowType} />

        { props.windowType!=='REGISTER_MODAL' &&
          <>
            <InputBox
              type={'email'}
              iconLeft={(<OwnIcon.IconEmail className={"input-icon"} />)}
              placeholder={"email"}
              onChange={(value)=>this.setState({email:value})}
            />
            <InputBox
              type={'password'}
              iconLeft={(<OwnIcon.IconPassword className={"input-icon"} />)}
              placeholder={"password"}
              onChange={(value)=>this.setState({password:value})}
              //errorText=
            />
            <div className="fox-input">
              <Button usePadding={false} onClick={this.onLoginManual} btnClass={"btn-primary"}>Sign in</Button>
            </div>

            <div className={"line-box"}><label>or</label></div>
          </>
        }

        <div className="fox-input">
          <RegisterInfo windowType={props.windowType} />
          <GoogleLogin
            clientId={googleClientId}
            render={(renderProps)=>(
              <Button onClick={renderProps.onClick} usePadding={false} btnClass={"btn-primary btn-google"}>
                <OwnIcon.IconGoogle className={"input-icon-medsos"} />
                <label>Google Sign in</label>
              </Button>
            )}
            onSuccess={this.handleSignin}
            onFailure={this.handleSignin}
          />
          <LoaderElipsis isFetching={state.isFetching} />
        </div>
      </div>
    )
    }
  }
}
ModalWindowLogin.defaultProps={
  children:null, // props type react-element
  onClosed:()=>null, // props type function callback
  isHeader:true, // props type bool
  windowType:'LOGIN_MODAL',
  redirectTo:'/board',
}
const mapStateToProps = (state) => ({
  ...state.UserLogin
})
const mapDispatchToProps = (dispatch) => ({
  SetUserLogin: (data,command) => dispatch(actionCreators.SetUserLogin(data,command)),
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps) (ModalWindowLogin));
