import React from 'react';
import {FlatList,ListingItem,LoaderProjectItem,NotFound} from 'components';
import {actionCreators} from '_action';

let isMounted;
class ItemBar extends React.Component{
  constructor(props){
    super(props);
    this.state={
      data:[],
    }
    this.onSearch=null;
  }
  componentWillMount=()=>{
    isMounted=true;
  }
  componentWillUnMount=()=>{
    isMounted=false;
  }
  componentDidMount=()=>{
    this.onGetListing(this.props);
  }
  componentWillReceiveProps=(nextProps)=>{
    if(this.props.categoryPos !== nextProps.categoryPos || this.props.typePos!==nextProps.typePos){
      setTimeout(()=>{
        this.onGetListing(nextProps);
      },500);
    }
    if(this.props.searchKey!==nextProps.searchKey){
      clearTimeout(this.onSearch);
      this.onSearch=setTimeout(()=>{
        this.onGetListing(nextProps);
      },1000);
    }
  }
  onGetListing=(props)=>{
    this.setState({data:[]});
    actionCreators.GetProductList({token:'',keywords:props.searchKey},({data,isFetching,isFound,isError})=>{
      this.setState({isFound,isFetching:true,isError});
      if(isMounted){
        if(isFound){
          this.setState({data:data.data,isFetching})
        }else if(isError){
          setTimeout(()=>{
            this.onGetListing(props);
          },5000);
        }
      }
    })
  }

  render(){
    const {props,state} = this;
    return(
      <section className={"item-bar main-right-bar"} >
        <div className={"project-header fox-flex fox-just-start fox-align-center"}>
          {/**<label className={"header-text"}>Sort by</label>**/}

        </div>
        <div className={"project-content"}>
          <FlatList className={"d-flex flex-wrap"}  data={state.data} render={({item,index})=>{
            return <ListingItem key={index.toString()} className={"fox-col-3-m"} item={item} index={index} to={{pathname:`/project-invention-now/${item.id}/view`}}/>
          }} />
          <LoaderProjectItem isFetching={state.isFetching} />
          <NotFound isShow={state.data.length<1 && state.isFound} />
        </div>
      </section>
    )
  }
}
ItemBar.defaultProps={
  data:[],
  categoryPos:0,
  typePos:0,
  searchKey:'',
}
export {ItemBar};
